import React, { Component } from "react";
import { withRouter } from "react-router-dom";

// Styles
import "./Alert.css";
import logo from "./../../assets/Logo.svg";

class Alert extends Component {
    constructor() {
        super();
    }

    render() {
        return(
        <div className={"alert-container"}>
            <div className={"alert-back-drop"}></div>
            <div className={"alert-content"}>
                <img className="alert-logo" src={logo} alt="Logo" />
                <p>Sorry, but weve detected that your browser or device is not compatible</p>
                <p>with our virtual meeting software.</p>
                <br/>
                <p>Please try a different browser or device to access the session.</p>
                <br/>
                <p>For more details on compatibility options, <a target="_blank" href="https://docs.aws.amazon.com/chime/latest/dg/meetings-sdk.html">click here.</a></p>
            </div>
        </div>
        );
        
    }
}

export default withRouter(Alert);